import axios from 'axios'

const API = axios.create({
    baseURL:`https://my-json-server.typicode.com/titantsai/tmrt`,
    withCredentials:false,
    headers:{
        Accept: 'application/json',
        'Content-Type': 'application/json'
    }
})

export default{
    getStations(route){
        return API.get(route)
    }
}